@property --rotate {
  syntax: "<angle>";
  inherits: false;
  initial-value: 0deg;
}
@-moz-document url-prefix() {
  :root {
    --rotate: 135deg;
  }
  * {
    scrollbar-color: #2c2c2c rgba(126, 126, 126, 0.4235294118);
    scrollbar-width: thin;
  }
}
* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*::-webkit-scrollbar {
  height: 7px;
  width: 7px;
  background-color: rgba(53, 53, 53, 0.4235294118);
}

*::-webkit-scrollbar-thumb {
  background-color: #2c2c2c;
}

*::-webkit-scrollbar-button:single-button {
  background-color: #2c2c2c;
  width: 17px;
}

*::-webkit-scrollbar-button:single-button:hover, *::-webkit-scrollbar-thumb:hover {
  background-color: #3f3f3f;
}

@media screen and (max-width: 800px) {
  *::-webkit-scrollbar {
    display: none;
  }
}
html {
  width: 100vw;
  background-color: #1a1a1a;
}

p, h2 {
  font-family: "Chakra Petch", sans-serif;
}

p {
  font-weight: 100;
}

h2 {
  font-variant: small-caps;
  font-size: 4rem;
  font-weight: 700;
  color: black;
  text-shadow: 0px 0px 5px #ffffff, 0px 0px 5px #ffffff;
}
@media screen and (max-width: 400px) {
