a, #switch {
  text-decoration: none;
  cursor: url("./hand-cursor-black.svg"), pointer;
}

body {
  position: relative;
  z-index: 0;
  width: 100%;
  height: -moz-max-content;
  height: max-content;
  cursor: url("./cursor-black.svg"), auto;
  background: linear-gradient(0deg, #96d3ff, #cbbfff);
  transition: all 0.9s ease;
}
body #up {
  position: absolute;
  top: 100vh;
  right: 0;
  bottom: 0;
  width: 5rem;
  height: calc(100% - 100vh);
  font-size: 2rem;
  pointer-events: none;
}
body #up a {
  position: relative;
}
body #up #up-icon {
  position: sticky;
  top: 1vh;
  right: 0;
  color: #000000;
  padding: 1rem;
  z-index: 99;
  pointer-events: all;
}
body #switch {
  position: fixed;
  top: 0;
  left: 0;
  font-size: 2rem;
  padding: 10px;
  z-index: 99;
}
body #switch:hover {
  color: #ffe8aa;
  transition: all 0.3s ease;
}

#welcome {
