  justify-content: space-between;
  align-items: flex-start;
  padding-bottom: 40px;
}
#welcome h1, #welcome p, #welcome a {
  text-shadow: 0px 0px 5px #ffffff, 0px 0px 5px #ffffff;
}
#welcome .welcome-bg {
  position: absolute;
  top: 0;
  bottom: -25vh;
  right: 0;
  left: 0;
  -webkit-mask-image: linear-gradient(to bottom, #000000 0%, #000000 70%, rgba(0, 0, 0, 0) 100%);
  mask-image: linear-gradient(to bottom, #000000 0%, #000000 70%, rgba(0, 0, 0, 0) 100%);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  transition: all 0.3s ease;
}
#welcome .dark-element {
  background-image: url("pictures/start_dark.webp");
}
#welcome .light-element {
  background-image: url("pictures/start_light.webp");
}
#welcome #nav-bar {
  display: flex;
  width: 100%;
  height: -moz-max-content;
  height: max-content;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 40px 0;
  justify-content: space-around;
  animation: slide-down 1s ease;
}
@keyframes slide-down {
  0% {
    transform: translate(0 -100%);
  }
  100% {
    transform: translate(0 0);
  }
}
#welcome #nav-bar a {
  position: relative;
  margin: 0;
  padding: 15px;
  font-size: 1.5rem;
  color: black;
  font-family: "Chelsea Market", cursive;
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0);
  transition: all 0.3s ease;
  overflow: hidden;
}
#welcome #nav-bar a::before {
  content: "";
  position: absolute;
  width: 15%;
  height: 200%;
  top: -95%;
  left: -15%;
  background-color: rgba(255, 255, 255, 0.1843137255);
  transform-origin: center right;
  transform: rotate(45deg);
}
#welcome #nav-bar a:hover {
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: rgba(0, 0, 0, 0.1019607843) 0px 0px 5px 0px, rgba(0, 0, 0, 0.1019607843) 0px 0px 1px 0px;
}
#welcome #nav-bar a:hover::before {
  animation: glare 0.8s ease;
}
@keyframes glare {
  0% {
    top: -95%;
    left: -15%;
  }
  100% {
    top: 15%;
    left: 100%;
  }
}
#welcome #text-container {
  position: relative;
  max-width: 70%;
  min-width: -moz-max-content;
  min-width: max-content;
  height: -moz-max-content;
  height: max-content;
  padding: 10px 0 30px 0;
  animation: slide-right 1s ease;
  z-index: 3;
  transition: color 0.3s ease;
}
@keyframes slide-right {
  0% {
    transform: translate(-100% 0);
  }
  100% {
    transform: translate(0 0);
  }
}
#welcome #text-container .text {
  max-width: 100%;
